'use client';

import { Metadata } from 'next';
import { useState } from 'react';
import { ChevronDown, ChevronUp, HelpCircle, Phone, Mail } from 'lucide-react';
import AnimatedPageHeader from '@/components/AnimatedPageHeader';
import AnimatedContentSection from '@/components/AnimatedContentSection';
import { motion, AnimatePresence } from 'framer-motion';

// Нельзя экспортировать metadata из клиентского компонента, поэтому устанавливаем title через useEffect
// export const metadata: Metadata = {
//   title: 'FAQ - Prime Balloons',
//   description: 'Часто задаваемые вопросы о доставке воздушных шаров и праздничном декоре.',
// };

interface FAQItem {
  question: string;
  answer: string;
}

const faqItems: FAQItem[] = [
  {
    question: 'Какие гарантии качества на воздушные шары?',
    answer: 'Мы гарантируем высокое качество всех наших воздушных шаров. Они производятся из прочного латекса или фольги и проходят строгий контроль качества. Мы предоставляем гарантию на все товары и готовы заменить изделие при обнаружении заводского брака.'
  },
  {
    question: 'Как долго держатся гелиевые шары?',
    answer: 'Латексные шары с гелием держатся в среднем 10-12 часов, фольгированные шары - до 2-3 недель. Для увеличения времени полёта латексных шаров мы рекомендуем обработку специальным средством Hi-Float, которое увеличивает время полёта до 3-5 дней.'
  },
  {
    question: 'Осуществляете ли вы доставку?',
    answer: 'Да, мы предлагаем услуги доставки по всему городу и области. Стоимость и время доставки зависят от вашего местоположения и объема заказа. Для получения точной информации, пожалуйста, свяжитесь с нашими менеджерами.'
  },
  {
    question: 'Можно ли заказать индивидуальное оформление?',
    answer: 'Да, мы предоставляем услуги по индивидуальному оформлению шарами различных мероприятий: дней рождения, свадеб, корпоративных событий и других праздников. Наши дизайнеры разработают уникальное оформление по вашим пожеланиям.'
  },
  {
    question: 'Можно ли напечатать логотип на шарах?',
    answer: 'Да, мы предоставляем услуги печати на воздушных шарах. Вы можете заказать шары с вашим логотипом, текстом или изображением. Минимальный тираж для заказа печати составляет 50 штук.'
  },
  {
    question: 'Как осуществляется оплата?',
    answer: 'Мы принимаем различные способы оплаты: наличными при получении, банковской картой через защищенный терминал, банковским переводом для юридических лиц. Также возможна оплата онлайн на нашем сайте.'
  }
];

export default function FAQPage() {
  const [openItems, setOpenItems] = useState<number[]>([]);

  const toggleItem = (index: number) => {
    setOpenItems(prev => 
      prev.includes(index) ? prev.filter(i => i !== index) : [...prev, index]
    );
  };

  return (
    <div className="container mx-auto px-4 py-16">
      <AnimatedPageHeader
        title="Часто задаваемые вопросы"
        description="Здесь вы найдете ответы на самые популярные вопросы о наших товарах и услугах"
        icon={HelpCircle}
        bgColor="bg-pink-100"
        iconColor="text-pink-500"
      />

      <div className="max-w-3xl mx-auto">
        {faqItems.map((item, index) => (
          <AnimatedContentSection
            key={index}
            delay={index * 0.1}
            animation={index % 2 === 0 ? 'fadeLeft' : 'fadeRight'}
            className="mb-4 bg-white rounded-xl shadow-sm overflow-hidden"
          >
            <motion.div
              className="cursor-pointer"
              onClick={() => toggleItem(index)}
              whileHover={{ scale: 1.01 }}
              transition={{ duration: 0.2 }}
            >
              <div className="p-5 flex justify-between items-center">
                <h3 className="text-lg font-semibold text-gray-800">{item.question}</h3>
                <motion.div
                  initial={false}
                  animate={{ rotate: openItems.includes(index) ? 180 : 0 }}
                  transition={{ duration: 0.3 }}
                  className="text-pink-500"
                >
                  {openItems.includes(index) ? <ChevronUp /> : <ChevronDown />}
                </motion.div>
              </div>
              <AnimatePresence>
                {openItems.includes(index) && (
                  <motion.div
                    initial={{ height: 0, opacity: 0 }}
                    animate={{ height: 'auto', opacity: 1 }}
                    exit={{ height: 0, opacity: 0 }}
                    transition={{ duration: 0.3 }}
                    className="overflow-hidden"
                  >
                    <motion.div 
                      className="p-5 pt-0 text-gray-600 border-t border-gray-100"
                      initial={{ y: -10 }}
                      animate={{ y: 0 }}
                      transition={{ duration: 0.2, delay: 0.1 }}
                    >
                      {item.answer}
                    </motion.div>
                  </motion.div>
                )}
              </AnimatePresence>
            </motion.div>
          </AnimatedContentSection>
        ))}
      </div>

      <AnimatedContentSection 
        delay={0.5}
        animation="bounce"
        className="mt-16 p-8 rounded-xl bg-pink-500 text-white max-w-3xl mx-auto"
      >
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-4">Не нашли ответ на свой вопрос?</h2>
          <p className="mb-6">Свяжитесь с нами, и мы с радостью поможем вам!</p>
          <motion.a
            href="/contacts"
            className="inline-block px-6 py-3 bg-white text-pink-500 font-medium rounded-full"
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            Связаться с нами
          </motion.a>
        </div>
      </AnimatedContentSection>
    </div>
  );
} 